import { DOCUMENT } from "@angular/common";
import { Component, Inject, OnInit, Renderer2, ViewEncapsulation } from "@angular/core";
import { Location } from '@angular/common';
import { RouteConfigLoadEnd, RouteConfigLoadStart, Router } from "@angular/router";
import { fadeInAnimation } from "../shared/animations/fade-in.animation";
import { isNullOrUndefined } from "../shared/functions/util";


@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.css'],
  animations: [fadeInAnimation],
  encapsulation: ViewEncapsulation.None
})
export class LayoutComponent implements OnInit {
  public isLoading: boolean = false;
  private plugin: boolean = false;


  public timeOut = 60 * 999;
  public timeOutWarn = 60 * 1;

  constructor(
    @Inject(DOCUMENT) private document: Document,
    private renderer: Renderer2,
    private router: Router,
    private location: Location  ) {

    // Spinner for lazyload modules
    this.router.events.forEach((event) => {
      if (event instanceof RouteConfigLoadStart) {
        this.isLoading = true;
      } else if (event instanceof RouteConfigLoadEnd) {
        this.isLoading = false;
      }
    });

   }



  ngOnInit(): void {

  }

  ngAfterViewInit(): void {
    //   this.runOnRouteChange();
  }

  isMaps(path: any) {
    var titlee = this.location.prepareExternalUrl(this.location.path());
    titlee = titlee.slice(1);
    if (path == titlee) {
      return false;
    }
    else {
      return true;
    }
  }

  runOnRouteChange(): void {
    if (window.matchMedia(`(min-width: 960px)`).matches && !this.isMac()) {
      // const ps = new PerfectScrollbar(elemMainPanel);
      // ps.update();
    }
  }

  isMac(): boolean {
    let bool = false;
    if (navigator.platform.toUpperCase().indexOf('MAC') >= 0 || navigator.platform.toUpperCase().indexOf('IPAD') >= 0) {
      bool = true;
    }
    return bool;
  }
  public openClose() {
    var setting = this.document.getElementById('fixed-plugin');
    if (this.plugin) {
      this.renderer.addClass(setting, 'show');
    } else {
      this.renderer.removeClass(setting, 'show');
    }
  }

  public sidebarColor(a: any) {
    if (a.stopPropagation) {
      a.stopPropagation();
    } else if (window.event) {
      window.event.cancelBubble = true;
    }

    var sidebar = this.document.querySelector('.sidebar');
    //  var sidebar_img_container = sidebar?.querySelector('.sidebar-background');
    var full_page = this.document.querySelector('.full-page');
    var sidebar_responsive = this.document.querySelector('body > .navbar-collapse');
    //  var full_page_background = this.document.querySelector(".full-page-background");
    var new_color = a.currentTarget.getAttribute("data-color");

    var parent = a.currentTarget.parentElement.children;
    for (var i = 0; i < parent.length; i++) {
      this.renderer.removeClass(parent[i], 'active');
    }

    this.renderer.addClass(a.currentTarget, 'active');


    if (!isNullOrUndefined(sidebar)) {
      this.renderer.setAttribute(sidebar, 'data-color', new_color);
    }
    if (!isNullOrUndefined(full_page)) {
      this.renderer.setAttribute(full_page, 'filter-color', new_color);
    }
    if (!isNullOrUndefined(sidebar_responsive)) {
      this.renderer.setAttribute(sidebar_responsive, 'data-color', new_color);
    }
    if (!isNullOrUndefined(new_color)) {
      localStorage.setItem('data-color', new_color);
    }
  }

  public backgroundColor(a: any) {
    if (a.stopPropagation) {
      a.stopPropagation();
    } else if (window.event) {
      window.event.cancelBubble = true;
    }

    var sidebar = this.document.querySelector('.sidebar');

    var parent = a.currentTarget.parentElement.children;
    for (var i = 0; i < parent.length; i++) {
      this.renderer.removeClass(parent[i], 'active');
    }

    this.renderer.addClass(a.currentTarget, 'active');

    var new_color = a.currentTarget.getAttribute("data-background-color");

    if (!isNullOrUndefined(sidebar)) {
      this.renderer.setAttribute(sidebar, 'data-background-color', new_color);
    }

    if (!isNullOrUndefined(new_color)) {
      localStorage.setItem('data-background-color', new_color);
    }
  }

  public backgroundImage(a: any) {
    if (a.stopPropagation) {
      a.stopPropagation();
    } else if (window.event) {
      window.event.cancelBubble = true;
    }

    var sidebar = this.document.querySelector('.sidebar');
    var sidebarBackground = this.document.querySelector('.sidebar-background');

    var parent = a.currentTarget.parentElement.children;
    for (var i = 0; i < parent.length; i++) {
      this.renderer.removeClass(parent[i], 'active');
    }

    this.renderer.addClass(a.currentTarget, 'active');

    var new_image = a.currentTarget.querySelector('img');


    if (!isNullOrUndefined(sidebar)) {
      this.renderer.setAttribute(sidebar, 'data-image', new_image.src);
      this.renderer.setStyle(sidebarBackground, 'background-image', `url(${new_image.src})`);
    }

    if (!isNullOrUndefined(new_image)) {
      localStorage.setItem('data-image', new_image.src);
    }

  }
}


